$c-light: #ffffff;
$c-dark: #5f458c;
$c-purple: #a67ee5;
$c-royal-blue: #3e72ee;
$c-cornflower: #90b8e2;
$c-placeholder: rgb(118, 118, 118);

$font: "Proxima Nova", "Arial", sans-serif;
$font-alt: "Radical",  "Arial", sans-serif;

$stop-scaling: 2000px;

$wide-screen: "min-aspect-ratio: 1680/650";

$wider: "min-aspect-ratio: 1441/760";
$narrower: "max-aspect-ratio: 1439/760";

$tablet: 1024px;
$mobile: 768px;

$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;

$index: 1.38871;

$default-transition: 0.3s cubic-bezier(0.55, 0, 0.1, 1);
$transition-500: .5s cubic-bezier(0.5, 0.81, 0.93, 0.99);
$transition-600: 0.6s cubic-bezier(0.04, 0.72, 0.6, 0.96);
$transition-1000: 1s cubic-bezier(0.04, 0.72, 0.6, 0.96);
$transition-1500: 1.5s cubic-bezier(0.04, 0.72, 0.6, 0.96);
